.space-wrapper {
  min-height: 1080px;
  max-height: 1440px;
  width: 100%;
  position: absolute; }

#spaceDelete {
  margin: 10px 0px 10px 0px; }

#posSave {
  margin: 10px 0px 10px 0px; }

#background {
  position: inherit;
  min-height: 100%;
  height: inherit;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover; }

img, .image {
  position: relative;
  z-index: 1;
  height: 100%;
  width: 100%; }

.content, .text {
  border-style: none;
  font-size: 16px; }

.button {
  z-index: 10;
  position: relative; }

.menu {
  min-height: 220px;
  max-height: 450px;
  padding: 24px; }

button, md-button {
  top: 55%; }

h1, h2 {
  text-align: center;
  background: -webkit-linear-gradient(#09cbf2 0%, #4993d1 35%, #ef67b2 75%, #e53797 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: 'Rock Salt', cursive; }

h2 {
  background: -webkit-linear-gradient(45deg, #09cbf2 0%, #4993d1 35%, #ef67b2 75%, #e53797 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; }

.create {
  background: -webkit-linear-gradient(#09cbf2 0%, #4993d1 35%, #ef67b2 75%, #e53797 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: 'Rock Salt', cursive; }

#logoutButton {
  margin: 10px;
  margin-left: 45%;
  width: 140px;
  background-color: white;
  z-index: 2;
  position: relative;
  text-align: center;
  border-radius: 3px;
  border-style: ridge;
  border-color: rgba(225, 225, 225, 0.3); }

#logoutButton2 {
  margin: 10px;
  width: 140px;
  background-color: white;
  margin-right: 10%;
  float: right;
  top: -50px;
  border-radius: 3px;
  border-style: ridge;
  border-color: rgba(225, 225, 225, 0.3); }

#spacesList {
  margin: auto;
  padding: 0; }

ul > div.space-to-choose {
  margin: 40px;
  border: 1px solid #dcdfe5;
  border-radius: 4px;
  width: 100px;
  height: 100px;
  display: inline-block;
  vertical-align: top;
  text-align: center;
  padding: 50px; }
  ul > div.space-to-choose a {
    background: -webkit-linear-gradient(#09cbf2 0%, #4993d1 35%, #ef67b2 75%, #e53797 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: 'Rock Salt', cursive; }

ul, li {
  list-style: none; }

div.gridster-div:hover button {
  display: block;
  top: 0px; }

div.gridster-div button {
  position: absolute;
  display: none; }

audio {
  margin: 100px 150px 100px 25px; }

p {
  background-color: rgba(225, 225, 225, 0.2);
  border-radius: 3px; }

.create-wrapper {
  margin: 0 auto;
  text-align: center; }

#contentText {
  height: 200px;
  width: 250px; }

#contentSubmit {
  margin: 10px; }

#navBar {
  height: 80px;
  text-align: center; }
  #navBar #title {
    background: -webkit-linear-gradient(20deg, #09cbf2 0%, #4993d1 35%, #ef67b2 75%, #ff5eee 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: 'Rock Salt', cursive;
    font-size: 42px;
    margin: 0; }

.menu-list {
  background: -webkit-linear-gradient(#09cbf2 0%, #4993d1 35%, #ef67b2 75%, #ff5eee 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: 'Rock Salt', cursive;
  font-size: 14px; }

button.btn {
  background: -webkit-linear-gradient(#09cbf2 0%, #4993d1 35%, #ef67b2 75%, #ff5eee 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: 'Rock Salt', cursive; }

#newSpaceButton {
  margin-left: 7%; }

button {
  border-radius: 3px;
  border-style: ridge;
  border-color: rgba(225, 225, 225, 0.3);
  background-color: white;
  margin: 2px; }
  button a {
    background: -webkit-linear-gradient(#09cbf2 0%, #4993d1 35%, #ef67b2 75%, #ff5eee 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: 'Rock Salt', cursive;
    font-size: 10px; }

input {
  border-radius: 4px;
  margin: 2px; }
  input .register {
    float: left; }
